import Vue from 'vue'
import Router from 'vue-router'
import About from '@/pages/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: {name: 'about'}
    },
    {
      path: '/',
      name: 'about',
      meta: {
        desc: '关于我们'
      },
      component: About
    },
    {
      path: '/join',
      name: 'join',
      meta: {
        desc: '加盟优势'
      },
      component: resolve => require(['@/pages/Join'], resolve)
    },
    {
      path: '/stores',
      name: 'stores',
      meta: {
        desc: '全国门店'
      },
      component: resolve => require(['@/pages/Stores'], resolve)
    },
    {
      path: '/contact',
      name: 'contact',
      meta: {
        desc: '联系我们'
      },
      component: resolve => require(['@/pages/Contact'], resolve)
    }
  ]
})



