<template>
  <section class="about-container">

    <swiper :options="swiperOption" class="swiper-box f-hf">
        <swiper-slide class="swiper-item f-wf" v-for="(o, i) in swiperlist" :key="o.src">
            <a href="javascript:;" class="f-ib f-wf f-hf swiper-link">
                <img width="100%" height="100%" :src="o.value"/>
            </a>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>

    <app-bg
      :height="296" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_01-min.jpg"/>

    <div class="video-box f-pr">
        <div class="video-img f-pr">
            <img class="f-pa poster" width="100%" height="100%" src="http://osbfr39w7.bkt.clouddn.com/wface.video.poster.png"/>
        </div>

        <div class="play-btn f-pa" v-show="!playing" @click="playVideo">
            <img width="100%" height="100%" :src="play_icon"/>
        </div>
        <video
         ref="videoEl"
         class="video f-pa"
         :style="{opacity: playing ? 1 : 0}"
         width="100%"
         preload
         src="https://img.beautybase.cn/video/%E6%89%8B%E6%B3%95%E6%8C%89%E6%91%A9%E5%88%9B%E6%84%8F%E8%A7%86%E9%A2%91-%E6%B0%B4%E5%8D%B0.mp4" controls="controls">
          您的浏览器不支持 video 标签。
        </video>
    </div>



    <app-bg
        :height="486" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_05-min.jpg"/>
    <app-bg
        :height="320" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_06-min.jpg"/>
    <app-bg
        :height="202" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_07-min.jpg"/>

    <app-bg
        :height="694" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_08-min.jpg"/>
    <app-bg
        :height="353" src="http://osbfr39w7.bkt.clouddn.com/wface/about/1_09-min.jpg"/>
  </section>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import Bus from '../../bus'
import AppBg from '@/components/AppBg'

export default {
  name: 'about',
  data() {
    return {
      play_icon: Bus.play_icon,
      swiperlist: Bus.swiperlist,
      playing: false,
      swiperOption: {
          autoplay: {
              delay: 4000,
              disableOnInteraction: false
          },
          loop: true,
          pagination: {
              el: '.swiper-pagination'
          }
       }
    }
  },
  created() {

  },
  mounted() {
  },
  methods: {
    playVideo() {
      this.playing = true
      this.$refs.videoEl.play()
    }
  },
  components: {
    swiper,
    swiperSlide,
    AppBg
  }
}
</script>

<style lang="less">
.about-container{
  .swiper-box{
    height: 250px;
    margin-top: -12px;
  }
  .video-box{
      width: 100%;
      height: 2.1rem;
  }
  .video-img{
    width: 100%;
    height: 100%;
  }
  .play-btn{
    width: 0.5rem;
    height: 0.5rem;
    left: calc(50% - 0.25rem);
    top: calc(50% - 0.25rem);
    z-index: 30;
  }
  .poster{
    z-index: 10;
    left:0;
    bottom: 0;
    right: 0;
    top: 0;
    height: 100%;
  }
  .video{
    left:0;
    bottom: 0;
    right: 0;
    top: 0;
    opacity: 0;
    z-index: 20;
  }
}
</style>



