<template>
  <div class="bg-wrapper f-pr" :style="{height: height / 100 + 'rem'}">
    <img class="bg-img" width="100%" height="100%" :src="src"/>
  </div>
</template>

<script>
export default {
  name: 'AppBg',
  data() {
    return {
      zIndex: 4
    }
  },
  props: {
    src: {
      type: String,
      default: ''
    },
    height: {
      type: Number,
      default: 300
    }
  },
  created() {
  },
  mounted() {
    console.log(this.fixed)
  },
  methods: {
    imgLoaded() {
      this.zIndex = 10;
    }
  }
}
</script>

<style scoped lang="less">
.bg-wrapper{
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  .bg-img {
    position: relative;
    z-index: 4;
  }
}
</style>



