import AppBg from '@/components/AppBg'
import Bus from '@/bus'
import { getStyle } from '@/libs/utils'
export default {
  name: 'App',
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
    setHtmlSize()
  },
  components: {
    AppHeader,
    AppTab,
    AppBg,
    AppFooter
  }
}
</script>

<style scoped lang="less">
  .app-wrapper{
    background-color: #f7f6f1;
  }
</style>



