<template>
  <div class="tab-container f-pa">
      <div class="tab-list f-row-around">
          <div class="tab-item f-row-center"
            v-for="(o, i) in tablists"
            :key="i"
            :style="{
              paddingBottom: i === current ? '5px' : '6px',
              borderBottomWidth: i === current ? '1px' : 0
            }" @click="switchTab(i, o)">
            <a class="tab-item-link">{{o.label}}</a>
          </div>
      </div>
  </div>
</template>

<script>
import Bus from '@/bus'
export default {
  name: 'AppTab',
  data() {
    return {
      tablists: Bus.tablists
    }
  },
  created() {

  },
  computed: {
    current: () => Bus.current_tab
  },
  methods: {
    switchTab(idx, o) {
      this.$router.push({name: o.value})
      Bus.current_tab = idx
    }
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.tab-container{
  width: 100%;
  bottom: 12px;
  left: 0;
  .tab-list{
    height: 0.38rem;
  }
  .tab-item{
    border-bottom: 0.01rem solid #fff;
  }
  .tab-item-link{
    font-weight: lighter;
    font-size: 0.13rem;
    color: #fff;
    width: 100%;
    height: 100%;
    display: inline-block;
  }
}
</style>



